import React, { PureComponent } from 'react';
import { Card, Button } from 'element-react';
import { withStyles } from '@material-ui/core/styles';
import globalStyles from '../../styles/global.css';

const { floatRight } = globalStyles;
const styles = () => {
  return {
    floatRight,
    card: {
      margin: 'auto',
      padding: '10px',
      width: '250px',
      display: 'inline-block'
    },
    image: {
      width: '60%'
    },
    title: {
      fontSize: '21px',
      fontWeight: 'bold'
    },
    description: {
      fontSize: '12px'
    },
    cardBottom: {
      paddingTop: '9px'
    }
  };
};

class ProductCard extends PureComponent {
  render() {
    const {
      image,
      product,
      title,
      description,
      buttonLabel,
      onBuy,
      classes
    } = this.props;

    let price;
    if (product) {
      price = (product.price / 100).toFixed(2);
      price += ` ${product.currency === 'eur' ? '€' : product.currency}`;
    }

    return (
      <Card className={classes.card}>
        <img alt="credits" src={image} className={classes.image} />
        <div style={{ padding: 14 }}>
          <span className={classes.title}>{title}</span>
          <div className={classes.cardBottom} id={this.props.id}>
            {price ? (
              <span>{price}</span>
            ) : (
              <span className={classes.description}>{description}</span>
            )}
            <Button type="text" className={classes.floatRight} onClick={onBuy}>
              {buttonLabel}
            </Button>
          </div>
        </div>
      </Card>
    );
  }
}

export default withStyles(styles)(ProductCard);
