/* eslint-disable react/no-array-index-key */
import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Zoom from '@material-ui/core/Zoom';
import CircularProgress from '@material-ui/core/CircularProgress';
import ProductAPI from '../../api/ProductAPI';
import styles from '../../styles/ProductList.css';
import ProductCard from './ProductCard';

import creditsImage from '../../assets/credits-4.png';

class ProductList extends Component {
  state = {
    subscribeProducts: [],
    checked: false,
    hasLoad: false
  };

  componentDidMount() {
    this.getProducts();
  }

  getProducts = () => {
    ProductAPI.getProducts()
      .then(resp => {
        const subscribeProducts = [];
        resp.forEach(product => {
          if (product.metadata.months) subscribeProducts.push(product);
        });
        this.setState({
          subscribeProducts,
          checked: true,
          hasLoad: true
        });
      })
      .catch(() => {
        this.setState({
          subscribeProducts: []
        });
      });
  };

  render() {
    const { classes } = this.props;
    const { checked, hasLoad } = this.state;

    const subscribeLayout = this.state.subscribeProducts.map(
      (product, index) => {
        return (
          <ProductCard
            key={`subscription-${index}`}
            image={creditsImage}
            product={product}
            title={product.name}
            buttonLabel="Buy yourself time"
            onBuy={() => this.props.history.push(`/buy?sku=${product.sku}`)}
          />
        );
      }
    );

    subscribeLayout.push(
      <ProductCard
        key={`contactUs-${subscribeLayout.length}`}
        image={creditsImage}
        title="Teams"
        description="Share models with your team."
        buttonLabel="Contact Us"
        onBuy={() => this.props.history.push('/support')}
      />
    );

    return (
      <>
        {!hasLoad ? (
          <CircularProgress size={100} className={classes.progress} />
        ) : (
          <div className={`${classes.bodyContainer} ${classes.center}`}>
            <div className={classes.centeredCtn}>
              <div className={classes.supportSection}>
                <span className={classes.supportSectionTitle}>
                  Time Subscription
                </span>
              </div>
              <Zoom
                in={checked}
                style={{ transitionDelay: checked ? '10ms' : '0ms' }}
              >
                <div>{subscribeLayout}</div>
              </Zoom>
            </div>
          </div>
        )}
      </>
    );
  }
}

export default withStyles(styles)(ProductList);
